
import 'styles/index.scss';



document.addEventListener('DOMContentLoaded', function(){

  const menu = document.getElementById('b-menu');
  const button = document.getElementById('b-button-icon');
  let iconMenu = document.getElementById('b-button-icon').src;
  let toggle = false;

  let locationSite = window.location.href;
  let imgSrc;
  let imgSrc2;
  if (~locationSite.indexOf("local")){
    console.log("localhost")
    imgSrc = './../assets/images/burger.svg';
    imgSrc2 = './../assets/images/close.svg';
  } else {
    imgSrc = './assets/images/burger.svg';
    imgSrc2 = './assets/images/close.svg';
    console.log("not localhost")
  }

  function classToggle() {
    menu.classList.toggle('b-close-menu');
    if (toggle === true) {
               document.getElementById('b-button-icon').src  = imgSrc;
        }
         else {
               document.getElementById('b-button-icon').src =  imgSrc2;
       }
    toggle = !toggle;
  }
  button.addEventListener('click', classToggle);

  /*dropdown*/
  var selectBtn = document.getElementsByClassName('b-dropdown'),
    dropdownMenu = document.getElementsByClassName('b-dropdownMenu');

for (var i = 0; i < selectBtn.length; i++) {
    selectBtn[i].onclick = function() {
        if(this.className.indexOf('b-active') > -1){
            for (var j = 0; j < selectBtn.length; j++) {
                removeClass(selectBtn[j], 'b-active')
            }
        } else {
            addClass(this, 'b-active');
        }
    };
}
for (var i = 0; i < dropdownMenu.length; i++) {
    var child = dropdownMenu[i].children;
    for (var j = 0; j < child.length; j++) {
        child[j].onclick = function() {
            var text = this.innerHTML;
            this.parentNode.previousElementSibling.children[0].innerHTML = text;
            toggleClass(this.parentNode, 'showMenu');
        };
    }
}

window.addEventListener('click', function(event){
    for (i = 0; i < selectBtn.length; i++) {
        if (event.target != selectBtn[i].children[0]){
            removeClass(selectBtn[i], 'b-active');
        }
    }
});




function toggleClass(el, classToToggle) {
    var classN = el.className;
    if(classN.indexOf(classToToggle) > -1){
        el.className =  classN.replace(" " + classToToggle, '');
	} else {
        el.className = classN + " " + classToToggle;
	}
}
function addClass(el, classToToggle) {
    var classN = el.className
    if(classN.indexOf(classToToggle) < 1){
        el.className = classN + " " + classToToggle;
    }
}
function removeClass(el, classToToggle) {
    var classN = el.className;
    if(classN.indexOf(classToToggle) > -1){
        el.className =  classN.replace(" " + classToToggle, '');
	}
}


    let accItem = document.getElementsByClassName('b-accordeon-item ');
    let accHD = document.getElementsByClassName('b-accordeon-item__head');
    for (let i = 0; i < accHD.length; i++) {
        accHD[i].addEventListener('click', toggleItem, false);
    }
    function toggleItem() {
        let itemClass = this.parentNode.className;
        for (let i = 0; i < accItem.length; i++) {
            accItem[i].className = 'b-accordeon-item b-close';
        }
        if (itemClass == 'b-accordeon-item b-close') {
            this.parentNode.className = 'b-accordeon-item b-open';
        }
    }






});
